<template>
  <div v-if="productStatus.isLoading"><PreloadSpin /></div>
  <div v-else-if="productStatus.isFailed">Не удалось загрузить товар</div>
  <div v-else>
    <div class="content__top">
      
    </div>

    <section class="item popup_content">
      <div class="item__pics pics">
        <div class="pics__wrapper">
          <img :src="productImage || '../img/no-image.jpg'" :alt="product.title">
        </div>
        
      </div>

      <div class="item__info">
        <span class="item__code">Артикул: {{product.id}}</span>
        <h2 class="item__title">
          {{product.title}}
        </h2>
        <div class="item__form">
          <form class="form" action="#" method="POST" @submit.prevent="doAddToCart">
            <div class="item__row item__row--center">
              <ProoductQnty :productAmount="productAmount" @productAmount-changed="updateQuantity"/>              
              <b class="item__price">
                {{ product.pricePretty }} ₽
              </b>
            </div>

            <div class="item__row">
              <fieldset class="form__block">
                <legend class="form__legend">Цвет</legend>
                <ul class="colors colors--black">
                  <li class="colors__item" v-for="(color, index) in product.colors" :key="index">
                    <label class="colors__label">
                      <input class="colors__radio sr-only" type="radio" name="color-item" :value="color.id" v-model.number="selectedColor">
                      <span class="colors__value" :style="{ backgroundColor: color.color.code }">
                      </span>
                    </label>
                  </li>
                </ul>
              </fieldset>  
              <fieldset class="form__block">
                <legend class="form__legend">Размер</legend>
                <label class="form__label form__label--small form__label--select" >
                  <select class="form__select" name="category" v-model="currentSize">
                    <option :value="sizes.id" v-for="sizes in size" :key="sizes.id" >{{sizes.title}}</option>
                  </select>
                </label>
              </fieldset>
            </div>
            <div class="popup_bottom">
            <button class="item__button button button--primery" type="submit" :disabled="productStatus.isSendingProduct">
              {{ productStatus.isSendingProduct ? 'Добавляем товар в корзину...' : 'В корзину' }}
            </button>
            </div>
            <BaseModal v-model:open="isShowAddedMessage">
            Товар добавлен в корзину</BaseModal>
          </form>
        </div>
      </div>
    </section>
    </div>

</template>

<script>
import ProoductQnty from '@/components/ProoductQnty.vue';
import PreloadSpin from '@/components/PreloadSpin.vue';
import { useStore } from 'vuex';
import BaseModal from '@/components/BaseModal.vue';
import {defineComponent } from 'vue';
import useProduct from '@/hooks/useProduct';
import useModal from '@/hooks/useModal';

export default defineComponent({
  props: {
      productId: {type: [Number, String], required: true}
  },

  components: {ProoductQnty, PreloadSpin, BaseModal},
  

  setup(props) {
    const {doClose} = useModal();
    const doCloseUp = () => {
      doClose();
    };
  const store = useStore();

  const {
    product,
    category,
    productAmount,
    currentSize,
    selectedColor,
    isShowAddedMessage,
    size,
    image: productImage,
    changeImage,
    isCurrentColor,
    updateQuantity,

    doAddToCart,
    fetchProduct,
    status: productStatus
    } = useProduct(store);

    
  fetchProduct(props.productId);

  return {
    productAmount,
    productData: product,
    productStatus,
    currentSize,
    selectedColor,
    isShowAddedMessage,
    product,
    category,
    size,
    productImage,
    doAddToCart,
    changeImage,
    isCurrentColor,
    updateQuantity,
    doCloseUp
  };
}

});

</script>


<style scoped>
  .item {
      grid-template-columns: 1fr;
  }
  .popup_content{
      flex-grow: 1;
      flex-shrink: 1;
      display: grid;
      grid-template-columns: 1fr 2fr;
      margin-bottom: 20px;
      width: 100%;
      height: 100%;
  }
  .popup__btn--close{
      margin-right: 20px;
  }
  .popup__btn{
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 15px 15px 12px;
      width: 160px;
  }
  .button--second{
      background-color: transparent;
      border: 1px solid #222;
      padding: 15px;
      color: #222;
      font-size: 14px;
  }
  .popup_bottom{
      flex-grow: 0;
      flex-shrink: 0;
      display: flex;
      justify-content: flex-end;
      align-items: stretch;
  }
</style>